$header-height : 60px;

$screen-sm 	: 768px;
$screen-md 	: 992px;
$screen-lg 	: 1200px;
$screen-xlg : 1600px;
$screen-xs 	: ($screen-sm - 1);
$screen-xxs : 425px;

.app-header {
  min-width: 320px;
  position: relative;
  height: $header-height;
  user-select: none;
  overflow: hidden;

  &-inner{
    color: #fff;
    background-color: #ed1c24;
    padding: 10px;
    min-width: 320px;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: $header-height;

    @media (min-width: $screen-md) {
      padding: 10px 20px;
    }
  }
}
